@import (less) "color.less";

:host( .ng-touched.ng-invalid, .nbp-show-validation.ng-invalid ) {
  .spinner-container {
    background-color: @white;
    border: 2px solid @red;
    border-radius: 2px;
  }
}

:host(.negotiation) {
  .spinner-input {
    font-size: 14px !important;
    font-weight: normal !important;
    color: @gray !important;
  }

  .spinner-button {
    width: 30px !important;
    height: 40px !important;
    background-color: @green !important;
  }
}

.spinner-container {
  background-color: @white;
  border: 2px solid @darkest_gray;
  border-radius: 2px;
  //overflow: auto;
  padding: 4px;
  //height: 50px;
  height: auto;
  overflow:auto;
  display: table;
  table-layout: fixed;
  width: 100%;
  // box-shadow: inset 1px 1px 2px 0px;
  
  &:disabled {
    background-color: @light_gray;
  }

  .left {
    width: 30px;
    text-align: left;
    display: table-cell;
    vertical-align: middle;
    span {
      vertical-align: 3px;
    }
  }

  .right {
    width: 30px;
    text-align: right;
    display: table-cell;
    vertical-align: middle;
  }

  .spinner-button {
    width: 30px;
    height: 32px;
    background-color: @green;
    font-size: 24px;
    line-height: 24px;
    color: white;
    border: none;
    border-radius: 2px;
    
    

    &:disabled {
      background-color: @light_gray;
    }

    
  }

  .spinner-input {
    
    border: none;
    height: 32px;
    text-align: center;
    font-size: 16px;
    color: @darkest_gray;
    //display: inline-block;
    width: 100%;
    
    
  }
  
  // placeholder style
  input[type="number"]::-webkit-input-placeholder {

    text-overflow: ellipsis !important;
  
  } 

  input[type="number"]:placeholder-shown {

    text-overflow: ellipsis !important;
  
  } 


  input:-ms-input-placeholder {

    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis !important;
  
  }

  input[type="number"]::-moz-placeholder {

    text-overflow: ellipsis !important;
  
  }

  input[type="number"]::placeholder
  {

    text-overflow: ellipsis !important;
  
  }


  .spinner-input-container {
    //width: calc(~'100% - 64px');
    display: table-cell;
    vertical-align: middle;
  }

  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
      -webkit-appearance: none;
  }
  
  input[type="number"] {
      -moz-appearance: textfield;
  }

  input[type="number"]::-ms-clear {
    display: none;
  }
}
